<template>
  <div class="bookshelf">
    <van-nav-bar title="书架" :fixed="true" :placeholder="true">
      <template #right>
        <p v-if="!edit" @click="onClickRight">编辑</p>
        <p v-else @click="onClickRight">完成</p>
      </template>
    </van-nav-bar>

    <div class="box">
      <ul>
        <li
          v-for="item in collect"
          :key="item.id"
          @click="!edit && toDetail(item.id)"
        >
          <van-badge>
            <template #content v-if="edit">
              <van-icon
                name="cross"
                class="badge-icon"
                @click.stop="removeCollect(item.id)"
              />
            </template>
            <div class="pic">
              <img :src="`http://statics.zhuishushenqi.com${item.img}`" />
            </div>
            <div class="tit">{{ item.title }}</div>
          </van-badge>
        </li>
      </ul>
    </div>

    <div class="empty" v-if="!collect.length">
      <div class="empty-box">
        <div class="info">
          <img src="../assets/images/shelf.png" />
          <div class="text">书架空空如也</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "Bookshelf",
  data() {
    return {
      edit: false,
    };
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.$emit("update:active", 2);
    });
  },
  computed: {
    ...mapState(["collect"]),
  },
  watch: {
    collect(n) {
      if (!n.length) {
        this.edit = false;
      }
    },
  },
  methods: {
    ...mapMutations(["removeCollect"]),
    onClickRight() {
      if (!this.collect.length) {
        return;
      }
      this.edit = !this.edit;
    },
    toDetail(id) {
      this.$router.push({
        name: "BookDetail",
        query: { id: id },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.bookshelf {
  padding-bottom: 50px;
  /deep/ .van-nav-bar {
    .van-nav-bar__title {
      font-weight: bold;
    }
    .van-nav-bar__text {
      color: #000;
    }
  }
  .box {
    ul {
      display: flex;
      flex-wrap: wrap;
      margin: 0 10px;
      li {
        flex-basis: 33.3%;
        margin: 10px 0;
        padding: 0 10px;
        .pic {
          height: 131px;
          width: 98px;
          border-radius: 10px;
          overflow: hidden;
          img {
            width: 100%;
            display: block;
          }
        }
        .tit {
          margin-top: 5px;
          font-size: 14px;

          word-wrap: break-word;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1; /*行数*/
          -webkit-box-orient: vertical;
        }
        /deep/ .van-badge {
          padding: 0;
          border: 0;
        }
      }
    }
  }
  .badge-icon {
    display: block;
    height: 16px;
    width: 16px;
    line-height: 16px;
  }
  .empty {
    width: 100vw;
    height: calc(100vh - 96px);
    text-align: center;
    .empty-box {
      width: 100%;
      height: 80%;
      display: flex;
      align-items: center;
      justify-content: center;
      .info {
        img {
          display: block;
          width: 144px;
          margin: 0 auto;
        }
        .text {
          color: #666;
        }
      }
    }
  }
}
</style>